@media (max-width: 896px) {
  .p-faq .-box-subscribe {
    margin-top: 60px !important;
  }
}

.p-faq .-box-subscribe > .inner {
  background: none;
}

.c-faq {
  margin-top: 50px;
  z-index: 2;
  position: relative;
}

.c-faq__inner {
  max-width: 850px;
  margin: 0 auto;
  padding-top: 40px;
}

@media (max-width: 896px) {
  .c-faq__inner {
    padding-top: 0;
  }
}

@media (max-width: 896px) {
  .c-faq__list {
    padding: 0 30px;
  }
}

.c-faq__item {
  padding-bottom: 50px;
  border-bottom: 1px solid #DCDCDC;
}

@media (max-width: 896px) {
  .c-faq__item {
    padding-bottom: 30px;
  }
}

.c-faq__item:first-of-type {
  border-top: 0;
}

.c-faq__item:not(:first-of-type) {
  margin-top: 50px;
}

@media (max-width: 896px) {
  .c-faq__item:not(:first-of-type) {
    margin-top: 30px;
  }
}

.c-faq__item.is-active .c-faq__icon {
  background: #211E1F;
}

.c-faq__item.is-active .c-faq__icon .fas.fa-chevron-down {
  opacity: 1;
  position: relative;
  color: #FFC416;
}

.c-faq__item.is-active .c-faq__icon .fas.fa-chevron-right {
  opacity: 0;
  position: absolute;
}

.c-faq__box-heading {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  cursor: pointer;
}

.c-faq__box-heading .c-faq__heading {
  max-width: 770px;
  font-size: 2.2rem;
  line-height: 1.4;
  color: #211E1F;
}

@media (max-width: 896px) {
  .c-faq__box-heading .c-faq__heading {
    font-size: 1.8rem;
    max-width: 80%;
  }
}

.c-faq__box-heading .c-faq__icon {
  width: 36px;
  height: 36px;
  border-radius: 100%;
  box-shadow: 0px 12px 33px 0px rgba(0, 0, 0, 0.1);
  color: #211E1F;
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.c-faq__box-heading .c-faq__icon .fas.fa-chevron-down {
  opacity: 0;
  position: absolute;
  transition: all 0.3s ease-out;
}

.c-faq__box-heading .c-faq__icon .fas.fa-chevron-right {
  opacity: 1;
  position: relative;
  transition: all 0.3s ease-out;
  color: #211E1F;
}

.c-faq__box-text {
  display: none;
}

.c-faq__box-text .c-faq__text {
  max-width: 770px;
  font-size: 1.6rem;
  color: #777777;
  padding-top: 20px;
}

@media (max-width: 896px) {
  .c-faq__box-text .c-faq__text {
    font-size: 1.4rem;
  }
}

.c-faq .c-heading-egg__icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.c-faq .c-heading-egg__icon > .c-comments-regular {
  width: 25px;
  height: 22px;
  background: url(/assets/img/svg/comments-regular.svg) no-repeat center center;
  background-size: cover;
}
